
.sidebar-main {
  position: fixed;
  left: 0;
  top: 0;
  width: 260px;
  height: 100%;
  background-color: #121212;
  color: #fff;
  >.inn {
    margin-left: 24px; }

  .logo-link {}
  color: inherit;

  // LOGO
  .sidebar-logo {
    color: #FFFFFF;
    margin-top: 24px;
    display: flex;
    align-items: center;
    .logo-img {
      margin-right: 8px; } }

  // BOARDS
  .sidebar-boards {
    margin-top: 64px;
    .title-min {
      margin-bottom: 8px;
      color: rgba(255, 255, 255, 0.50);
      font-size: 12px;
      display: block; } }
  .sidebar-create-new-board {
    color: #FFFFFF;
    margin-right: 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 14px 0;
    font-size: 14px;
    border-top: 1px solid rgba(255,255,255,0.1);
    border-bottom: 1px solid rgba(255,255,255,0.1);
    .new-board-btn {
      width: 40px;
      height: 36px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 20px;
      border-radius: 6px;
      background: #BEDBB0;
      line-height: 1;
      color: #121212; } }

  .sidebar-projects-list {
    margin-top: 40px;
    margin-bottom: 40px; }

  .project-item-main {
    padding: 20px 20px 20px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    color: rgba(255,255,255,0.5);
    position: relative;
    overflow: hidden;
    & path {
        stroke: rgba(255,255,255,0.5); }
    &.selected {
      color: rgba(255,255,255,1);
      path {
        stroke: rgba(255,255,255,1); }
      &:after {
        content: "";
        width: 4px;
        height: 100%;
        position: absolute;
        top: 0;
        right: 0;
        background: #BEDBB0;
        border-bottom-left-radius: 2px;
        border-top-left-radius: 2px; } }


    .flex-elems {
      display: flex;
      align-items: center; }
    .icon-wrap {
      margin-right: 8px;
      display: inline-block; }
    p {
      line-height: 1; }
    .project-actions-list {
      display: flex;
      align-items: center;
      li {
        cursor: pointer; }
      li:last-child {
        margin-left: 8px; } } }

  // SIDEBAR LOGOUT
  .sidebar-logout {
    color: #FFFFFF;
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 24px;
    left: 24px;
    cursor: pointer;
    .icon-wrap {
      margin-right: 14px; } } }



